@import url("https://fonts.googleapis.com/css2?family=Kanit:wght@400;500&display=swap");

body,
html {
  height: 100%;
  user-select: none;
  box-sizing: border-box;
  font-family: "Kanit", sans-serif;
}
body {
  background: #212121;
  padding: 20px;
}

.float-left{
  float: left;
}
.float-right{
  float: right;
}
.row::after{
  content: "";
  clear: both;
  display: table;
}
.col-left{
  float: left;
  width: 40%;
}
a{
  color: #149414;
  padding: 0;
  margin: 0;
  margin-top: 5px;
  text-decoration: none;
  opacity: 0.7;
}
a:hover{
  opacity: 1;
}
.container {
  text-align: center;
  border: 1px solid #303030;
  padding: 20px;
  width: 500px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
@media only screen and (max-width: 500px) {
  .container {
    width: auto;
  }
}
header {
  margin-bottom: 50px;
}
h2 {
  position: relative;
  color: #149414;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 25px;
  overflow: hidden;
  border-radius: 20px;
  font-weight: bold;
  text-align: center;
}
input {
  position: relative;
  padding: 1em 1em;
  outline: none;
  border: 1px solid #303030;
  background: #cac9c9;
  color: #149414;
  letter-spacing: 2px;
  font-size: 17px;
  overflow: hidden;
  transition: 0.2s;
  border-radius: 20px;
  font-weight: bold;
  width: 450px;
}
input::placeholder {
  color: #14941483;
}
@media only screen and (max-width: 480px) {
  .container{
    width: 80%;
  }
  input {
    width: 90%;
    font-size: 14px;
  }
  h2{
    font-size: 15px;
    letter-spacing: 0px;
  }
}
button {
  position: relative;
  padding: 0.7rem 1rem;
  outline: none;
  border: 1px solid #303030;
  background: #212121;
  color: #149414;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 17px;
  overflow: hidden;
  transition: 0.2s;
  border-radius: 20px;
  cursor: pointer;
  font-weight: bold;
  font-family: "Kanit", sans-serif;
  margin-top: 40px;
  width: 155px;
}

@media only screen and (max-width: 500px) {
  button {
    width: auto;
    font-size: 12px;
  }
  .col-2{
    width: 100%;
  }
}

button:hover {
  box-shadow: 0 0 10px #149414, 0 0 25px #0e6b0e, 0 0 50px #149414;
  transition-delay: 0.6s;
}

button span {
  position: absolute;
}
.container .span {
  position: absolute;
}

button span:nth-child(1) {
  top: 0;
  left: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, #149414);
}

button:hover span:nth-child(1) {
  left: 100%;
  transition: 0.7s;
}

button span:nth-child(3) {
  bottom: 0;
  right: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, #0e6b0e);
}

button:hover span:nth-child(3) {
  right: 100%;
  transition: 0.7s;
  transition-delay: 0.35s;
}

button span:nth-child(2) {
  top: -100%;
  right: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(180deg, transparent, #149414);
}

button:hover span:nth-child(2) {
  top: 100%;
  transition: 0.7s;
  transition-delay: 0.17s;
}

button span:nth-child(4) {
  bottom: -100%;
  left: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(360deg, transparent, #0e6b0e);
}

button:hover span:nth-child(4) {
  bottom: 100%;
  transition: 0.7s;
  transition-delay: 0.52s;
}

button:active {
  box-shadow: 0 0 10px #149414, 0 0 25px #0e6b0e, 0 0 50px #149414;
  background: #149414;
  background: linear-gradient(to top right, #149414, #0e6b0e);
  color: #bfbfbf;
  box-shadow: 0 0 8px #149414, 0 0 8px #0e6b0e, 0 0 8px #149414;
  transition: 0.1s;
}

button:active
  span:nth-child(1)
  span:nth-child(2)
  span:nth-child(2)
  span:nth-child(2) {
  transition: none;
  transition-delay: none;
}
